import { memo } from "react";
import './Contact.css';
import SectionTitle from '../../component/SectionTitle/SectionTitle'
const Contact = () => {
    return (
        <>
            <div className="contact mt-20 mb-20">
                <SectionTitle title="Contact Us"/>
                <div className="container">
                    <div className="row flex-wrap mt-10">
                        <div className="map lg:w-1/2 md:w-full">
                            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d12469.776493332698!2d-80.14036379941481!3d25.907788681148624!2m3!1f357.26927939317244!2f20.870722720054623!3f0!3m2!1i1024!2i768!4f35!3m3!1m2!1s0x88d9add4b4ac788f%3A0xe77469d09480fcdb!2sSunny%20Isles%20Beach!5e1!3m2!1sen!2sth!4v1642869952544!5m2!1sen!2sth" title='map' width="100%" height="200px" frameBorder="0" allowFullScreen=""></iframe>
                        </div>
                        <div className="contact-map lg:w-1/2 lg:px-5">
                            <h1 className="my-3 text-3xl font-bold">Contact With Us</h1>
                            <p className="mb-4">You can contact with us to share your thoughtes ,prefferd things, ideas for new features and rating our website just click send message button and it will redirect you to gmail for sending your words, fell free to write what you want and we will response as soon as we can.</p>
                            <button className="bg-red-600 text-white p-3 text-center m-auto rounded-md block"><a href="mailto:husseiinalrstoom@gmail.com" title="send message">Send Message</a></button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default memo(Contact);